import { handleActions } from 'redux-actions';
import * as actions from '../actions';

export default handleActions(
    {
        [actions.fetchTokenSuccess](state, { payload }) {
            return {
                ...state,
                cookie: payload,
            };
        },
        [actions.logoutSuccess](state) {
            return {
                ...state,
                cookie: {},
            };
        },
    },
    {
        cookie: {},
    }
);
